.control {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 2px;
}

.control__button {
  padding: 0;

  font-family: @font-deafult;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: @title-color;
  text-transform: uppercase;

  background: none;
  border: none;
  cursor: pointer;

  &--before {
    padding-left: 2px;
    text-align: right;
  }

  &--after {
    text-align: left;
  }
}

.control__bar {
  position: relative;
  width: 84px;
  height: 12px;
  margin-right: 18px;
  margin-left: 18px;
  padding: 2px 6px;

  background-color: @white;
  border: 1px solid @grey--dark;
  border-radius: 6px;
}

.control__indicator {
  width: 35px;
  height: 6px;

  background-color: @green;
  border-radius: 3px;
}

.control__default-track {
  position: relative;

  display: none;
  width: 428px;
  height: 6px;
  margin-right: 19px;
  margin-left: 19px;
  background-color: @grey;
  border-radius: 2.5px;
}

.control__default-thumb {
  position: absolute;
  top: -13px;
  left: calc(50% - 16px);

  width: 32px;
  height: 32px;

  background-color: @green;
  border: 12px solid @white;
  border-radius: 50%;
  box-shadow:
    0 0 0 1px @grey,
    0 2px 5px 0 fade(@black, 15);
  cursor: pointer;

  &:hover {
    box-shadow:
      0 0 0 1px @grey,
      0 3px 5px 0 fade(@black, 25);
  }

  &:active {
    box-shadow:
      0 0 0 2px @green,
      0 4px 5px 0 fade(@black, 30);
  }
}

@media (min-width: @tablet-width) {
  .control {
    padding-left: 0;
  }

  .control__bar {
    display: none;
  }

  .control__default-track {
    display: block;
  }

  .control__button {
    margin: 5px 3px 0 3px;
    padding: 0 0 6px 0;

    font-size: 20px;

    border-bottom: 1px dotted fade(@black--light, 23);

    &:hover,
    &:focus {
      border-bottom: 1px solid fade(@black--light, 40);
    }

    &:active {
      border-bottom: 1px solid transparent;
    }
  }
}

@media (min-width: @desktop-width) {
  .control {
    justify-content: flex-end;
  }

  .control__default-track {
    margin-left: 23px;
  }

  .control__button {
    margin: 5px 1px 0 1px;
    padding: 0 0 6px 0;
  }
}
